import React from "react"
import { fnListMyPage } from "src/helpers/fnList"
import { KConfigPageTable, KConfigTableHeader } from "src/helpers/K"

class MyTableHeaderExt extends React.Component {
  static propTypes = {}
  static defaultProps = {}
  constructor(props) {
    super(props)
    this.state = {}
  }
  getDefaultStyleHeader = () => {
    const { fnList } = this.props
    let _styleHeader =
      fnListMyPage.fnGetConfigPageTable(fnList, [
        KConfigPageTable.styleHeader,
      ]) || {}
    return _styleHeader
  }
  getStyle = ({ dfStyle, moreStyle } = {}) => {
    const { type, header, fieldName, headerObj, extraData } = this.props
    let _styled = dfStyle || {}
    if (headerObj && headerObj[KConfigTableHeader.style]) {
      _styled = Object.assign(
        _styled,
        this.getDefaultStyleHeader(),
        headerObj[KConfigTableHeader.style],
        moreStyle
      )
    }
    return _styled
  }
  render() {
    const { type, header, fieldName, headerObj, extraData } = this.props
    return <div></div>
  }
}

export default MyTableHeaderExt
