import React, { Component, Fragment } from "react"
// import { Tooltip } from "reactstrap"
import Tooltip from "rc-tooltip"
import styled from "styled-components"
import { MyUI } from "@macashipo/mlib"
import MyTableHeaderExt from "./_ext"

const WrapLayout = styled.div``

class MTHtooltip extends MyTableHeaderExt {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    const { headerObj, header } = this.props
    let _style = this.getStyle(this.props)
    const innerStyle = headerObj.tooltip_html
      ? { padding: 0, minHeight: "unset" }
      : {}
    const overlay = headerObj.tooltip_html ? (
      <MyUI type="html" html={headerObj.tooltip_html} />
    ) : (
      headerObj.tooltip
    )
    return (
      <Tooltip
        overlay={overlay}
        overlayInnerStyle={innerStyle}
        showArrow={false}
        placement="topLeft"
      >
        <div className="mth-tooltip" style={_style}>
          {header}
        </div>
      </Tooltip>
    )
  }
}
export default MTHtooltip
